/* You can add global styles to this file, and also import other style files */
* {
  box-sizing: border-box;
  margin: 0;
  outline: 0;
  font-family: 'Poppins', sans-serif;
}
html {
  width: 100vw;
  height: 100vh;
}
body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  background: #001935;
//  zoom: 0.666;
}
p,
div {
  color: #171717;
}
:root {
  --primary-color: #002949;
  //  --primary-color: #78cc6d;
  --app-border-radius: 4px;
  --app-box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
  --app-profile-width: 440px;
  --app-profile-height: 440px;
  --app-vg-card-width: 620px;
  --app-vg-card-height: calc(var(--app-profile-height) - 24px);
  --app-profile-header-height: 250px;
  --app-profile-padding: 210px 50px 0 50px;
}

.border-line-v {
  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 100%;
    background: -moz-radial-gradient(top, ellipse cover, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
    background: -webkit-radial-gradient(top, ellipse cover, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
    background: radial-gradient(ellipse at top, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
  }
}

.border-line-h {
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background: -moz-radial-gradient(left, ellipse cover, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
    background: -webkit-radial-gradient(left, ellipse cover, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
    background: radial-gradient(ellipse at left, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
  }
}

.page-wrapper {
  width: var(--app-vg-card-width);
  max-width: 650px;
  height: var(--app-vg-card-height);
  box-shadow: var(--app-box-shadow);
  border-radius: 0 var(--app-border-radius) var(--app-border-radius) 0;
  background: #ffffff;
  margin-top: 15px;
}

.vg-card {
  width: var(--app-vg-card-width);
  max-width: 650px;
  height: var(--app-vg-card-height);
  padding: 30px;
  overflow-y: auto;
  .vg-card__content {
    color: #000;
    .vg-card__content__title {
      margin: 0 0 15px 0;
      padding: 0 0 5px 0;
      position: relative;
      font-size: 17px;
      color: #171717;
      line-height: 21px;
      font-weight: 500;
      &::after {
        content: '';
        position: absolute;
        left: -30px;
        bottom: -15px;
        width: 95%;
        height: 1px;
        background: radial-gradient(ellipse at left, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
      }
    }
    .vg-card__content__text {
      position: relative;
      padding: 20px;
    }
  }
}

@media (max-width: 1199px) {
  :root {
    --app-vg-card-width: 455px;
  }
}

@media (max-width: 992px) {
  :root {
    --app-vg-card-width: 406px;
  }
  .vg-card {
    height: auto;
  }
  .page-wrapper {
    height: auto;
  }
}

