#starfield {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
}


@media (min-width: 992px) {
    :root {
    --app-profile-width: 33vw;
    --app-profile-height: 60vh;
    --app-profile-padding: 22vh 4vw 0 4vw ;
    --app-vg-card-width: 45vw;
  }
}

@media (min-width: 992px) and (-webkit-min-device-pixel-ratio: 1.25) {
  :root {
  --app-profile-height: 75vh;
 }
}

@media (min-width: 992px) and (-webkit-min-device-pixel-ratio: 1.5) {
  :root {
  --app-profile-padding: 14vh 4vw 0 4vw ;
  --app-profile-header-height: 200px;
  --app-profile-width: 26vw;
  --app-card-width: 40vw;
}
}

@media (min-width: 1400px){
  :root {
  --app-profile-width: 30vw;
  --app-profile-height: 67vh;
  --app-vg-card-width: 39vw;

  }
}

@media (min-width: 1400px) and (-webkit-min-device-pixel-ratio: 1.25) {
  :root {
  --app-profile-height: 75vh;
}
